<template>
	<div>
		<CCard>
			<CCardHeader>
				<b>Data</b> Stock In Out
			</CCardHeader>
			<CCardBody>
				<CRow>
					<CCol sm="12">
						<v-client-table
							:data="stockinout"
							:columns="tableFields"
							:options="tableOptions"
							id="stockinout-table"
						>
							<div slot="aksi" slot-scope="props">
								<router-link :to="'/stock-in-out/edit/' + props.row.id" class="text-primary btn btn-secondary btn-sm mr-2">
									<i class="fa fa-edit"></i>
								</router-link>
								<button class="text-danger btn btn-secondary btn-sm" @click="confirmStockInOut(props.row.id)">
									<i class="fa fa-trash"></i>
								</button>
							</div>
						</v-client-table>
					</CCol>
				</CRow>
			</CCardBody>
		</CCard>
		<CModal
		  title="Hapus Data Stock In Out"
		  size="sm"
		  color="danger"
		  :show.sync="smallModal"
		>
				Kwitansi ini akan dihapus secara permanen?
				<template #footer>
			       <CButton @click="deleteStockInOut()" color="danger">Hapus</CButton>
			       <CButton @click="smallModal = false" color="secondary">Batal</CButton>
			    </template>
		</CModal>
	</div>
</template>
<script type="text/javascript">
	export default {
		name:'StockInOut',
		data() {
			return {
				smallModal:false,
				id:0,
				stockinout:[],
				// tableFields: [
				// 	'no', 'kode_barang', 'nama_barang', 'spesifikasi', 'jumlah', 'tanggal', 'keterangan', 'status', 'aksi'
				// ],
				tableFields: ['no','nama_barang', 'type', 'qty', 'tanggal', 'alasan', 'aksi'],
				tableOptions: {
					perPage:10,
					pagination:{chunk:10, dropdown:false, edge:true, nav:'fixed'},
					headings: {
						no: 'No',
						nama_barang: 'Nama Barang',
						tanggal: 'Tanggal',
						type: 'Tipe',
						qty: 'Quantity',
						alasan: 'Alasan',
						aksi: 'Aksi'
					},
					sortable: ['no', 'nama_barang'],
					columnsClasses: {
						no:'text-center align-middle',
						type:'align-middle',
						nama_barang:'align-middle',
						qty:'text-center align-middle',
						tanggal:'text-center align-middle',
						alasan:'text-center align-middle',
						aksi:'text-center align-middle'
					}
				},
			}
		},
		methods: {
			getData() {
				this.$http.get('https://young-temple-67589.herokuapp.com/api/stock', {
					headers: {
						'Authorization': 'bearer ' + localStorage.token
					}
				})
				.then(res => {
					console.log(res.data)
					if(res.length > 0)
						this.stockinout = res.data
						for(let i = 0;i < res.data.length;i++) {
							this.stockinout[i].no = i + 1
						}
				})
				.catch(e => {
					alert('Tidak bisa ambil data :(')
					console.error(e)
					return false
				})
			},
			confirmStockInOut(id) {
				this.id = id
			},
			deleteStockInOut() {
				this.$http.delete('https://young-temple-67589.herokuapp.com/api/stock/' + this.id, {
					headers: {
						'Authorization':'bearer ' + localStorage.getItem('token')
					},
					redirect:'follow'
				})
				.then(res => {

					alert(res.data.message)
					this.smallModal = false
					this.getData()
				})
				.catch(e => {
					this.smallModal = false
					if(e.response.status == 401) {
	                  this.$store.dispatch('logout')
	                  .then(() => {
	                    let path = window.location.href
	                    path = path.split('/')
	                    localStorage.setItem('prevPath', path[path.length - 1])
	                    alert('Session Login kamu sudah habis! silahkan login kembali')
	                    
	                  })
	                  .then(() => {
	                    this.$router.replace({path: '/login'})
	                  })
	                  .catch(e => {
	                    alert('An error occured when get data :(')
	                    return false
	                  })
	                }
	                else {
	                  console.log(e)
	                  alert('Ada sedikit error, harap hubungi pengembang softwarenya yah :)')
	                  return false 
	                }
					return false
				})
			}
		},
		created() {
			this.getData()
		}
	}
</script>
<style type="text/css">
	#stockinout-table {
		width: 100%;
	}
	#stockinout-table .form-inline label {
		display: none;	
	}	
	#stockinout-table .form-inline input[type=text] {
		width: 130%;
	}
	#stockinout-table .form-inline {
		
		display: flex;
		width: 100%;
		justify-content: flex-start;
	}
	.VuePagination {
		margin-top: 20px;	
		display: flex;
		justify-content: center;
	}
</style>